import { writable, derived } from "svelte/store";
import { deriveUnwrap, deriveLastDefined } from "../utils/stores";
import { audioTimingsStore } from "./audioSelection";
import { allSectionsStore } from "../text/textState";
import { clamp } from "../utils/list";
import * as Tone from "tone";
/** Is the audio actively playing */
let playing = false;
const playingStoreInternal = writable(playing);
export const playingStore = playingStoreInternal;
playingStoreInternal.subscribe(state => playing = state);
Tone.Transport.on("start", () => playingStoreInternal.set(true));
Tone.Transport.on("stop", () => playingStoreInternal.set(false));
/** How loud should the audio play? (%) */
export const volumeStore = writable(100);
/** Should audio start playing when you select a new section? */
let autoPlay = true;
export const autoPlayStore = writable(autoPlay);
autoPlayStore.subscribe(state => autoPlay = state);
/** Should audio restart when ending? */
let loop = false;
export const loopStore = writable(loop);
loopStore.subscribe(state => {
    loop = state;
    Tone.Transport.loop = loop;
    if (!loop)
        stopAudio();
});
/** Which section idx is currently playing? */
const currentlyPlayingSectionIdxStoreInternal = writable(undefined);
export const currentlyPlayingSectionIdxStore = derived([playingStore, currentlyPlayingSectionIdxStoreInternal, audioTimingsStore], ([playing, currentIdx, audioTimings]) => {
    if (!playing)
        return undefined;
    if (!currentIdx)
        return undefined;
    if (!audioTimings)
        return undefined;
    if (currentIdx < audioTimings.start.sectionIdx)
        return undefined;
    if (currentIdx > audioTimings.end.sectionIdx)
        return undefined;
    return currentIdx;
});
/** Which section store is currently playing? */
const audioCurrentSectionStoreWrapped = derived([currentlyPlayingSectionIdxStoreInternal, allSectionsStore], ([idx, sections]) => {
    if (idx === undefined)
        return undefined;
    return sections[idx];
});
/** Which section is currently playing? */
export const currentlyPlayingSectionStore = deriveUnwrap(audioCurrentSectionStoreWrapped);
/** Which section is playing now, or if none, playing most recently? */
export const lastPlayedSectionStore = deriveLastDefined(currentlyPlayingSectionStore, undefined);
/** The duration, in seconds, of the audio file */
let duration;
/** Load the audio into the Tone.js transport */
async function createPlayer(demo) {
    const audioFile = demo ? "assets/demo.mp3" : "assets/audio.mp3";
    const gainNode = new Tone.Gain(1).toDestination();
    volumeStore.subscribe(volume => gainNode.gain.value = volume / 100);
    return new Promise(resolve => {
        const player = new Tone.Player(audioFile, () => {
            duration = player.buffer.duration;
            resolve(player);
        }).sync().start(0).connect(gainNode);
    });
}
/** Initialise the audio, loading the audio file and registering the start/end times of each section. Must be called before any play/pause methods. */
export async function initAudio(allSections, demo) {
    await createPlayer(demo);
    Tone.Transport.loop = true;
    Tone.Transport.loopEnd = duration;
    Object.entries(allSections).forEach(([idxString, section]) => {
        const idx = parseInt(idxString);
        Tone.Transport.schedule(() => currentlyPlayingSectionIdxStoreInternal.set(idx), section.startTime);
        Tone.Transport.schedule(() => currentlyPlayingSectionIdxStoreInternal.set(undefined), section.endTime);
    });
}
let desiredEnd = undefined;
/**
 * Plays the audio based on the current state.
 *
 * If the current timings have the start and end time the same, add 0.2 seconds to either side.
 * If the audio is already playing and the current time is inside the new timings, just continue playing.
 * Otherwise, restart from the start time specified in state.
 */
export function playAudio() {
    if (audioTimings === undefined)
        return;
    Tone.start();
    let start = clamp(audioTimings.start.time, 0, duration);
    let end = clamp(audioTimings.end.time, 0, duration);
    if (start === end) {
        start -= 0.2;
        end += 0.2;
    }
    ;
    Tone.Transport.loop = false;
    Tone.Transport.loopStart = start;
    Tone.Transport.loopEnd = end;
    if (Tone.Transport.state === "started") {
        Tone.Transport.seconds = start;
    }
    else {
        Tone.Transport.start(undefined, start);
    }
    Tone.Transport.loop = loop;
    currentlyPlayingSectionIdxStoreInternal.set(audioTimings.start.sectionIdx);
    if (!loop) {
        desiredEnd = end;
        Tone.Transport.scheduleOnce(() => {
            if (desiredEnd !== end)
                return; // Changed desired end, do nothing.
            if (loop)
                return;
            Tone.Transport.stop();
        }, end);
    }
}
/** Stop the audio if currently playing */
export function stopAudio() {
    if (playing) {
        Tone.Transport.stop();
    }
}
export const suppressAudioStore = writable(false);
/** The desired start and end time if the audio were to start playing now */
let audioTimings = undefined;
derived([suppressAudioStore, audioTimingsStore], ([suppress, timings]) => ({ suppress, timings })).subscribe(({ suppress, timings }) => {
    audioTimings = timings;
    if (suppress)
        return;
    if (timings === undefined)
        return stopAudio(); // Stop if the new state contains no timings
    if (autoPlay)
        return playAudio(); // Start if autoplay is enabled
    else
        stopAudio();
});
