import { writable } from "svelte/store";
let toastIdx = 0;
const toastDuration = 2.5;
const toastStoreInternal = writable([]);
/** Store containing the toasts currently visible on the screen */
export const toastStore = toastStoreInternal;
/** Display a toast notification on the screen */
export function sendToast(text) {
    toastStoreInternal.update(state => {
        state.push({ idx: toastIdx, text });
        return state;
    });
    toastIdx++;
    setTimeout(() => {
        toastStoreInternal.update(state => {
            state.shift();
            return state;
        });
    }, toastDuration * 1000);
}
