import { allSectionsStore } from "./textState";
import { deriveUnwrapRecord } from "../utils/stores";
import { saveAs } from "file-saver";
let state = {};
deriveUnwrapRecord(allSectionsStore).subscribe(sections => state = sections);
export function exportTranscript() {
    const keys = Object.keys(state).map(key => parseInt(key));
    keys.sort((a, b) => a - b);
    const sections = keys.map(key => state[key]).filter(section => section !== undefined);
    const output = exportTranscriptSubtitles(sections);
    const blob = new Blob([output], { type: "text/plain;charset=utf-8" });
    saveAs(blob, "lexoralExport.srt");
}
export function exportTranscriptPlainText(sections) {
    return sections.map(sectionToPlainText).reduce((acc, elem) => acc + elem, "");
}
function sectionToPlainText(section) {
    return `${section.edited ? section.text : section.placeholder}${section.endParagraph ? "\n" : " "}`;
}
export function exportTranscriptSubtitles(sections) {
    let startTime = undefined;
    let text = "";
    const subtitles = [];
    for (const section of sections) {
        if (startTime === undefined) {
            startTime = section.startTime;
        }
        text += " ";
        text += (section.edited ? section.text : section.placeholder);
        if (section.endParagraph) {
            subtitles.push({
                startTime,
                endTime: section.endTime,
                text: text.trim()
            });
            startTime = undefined;
            text = "";
        }
    }
    return subtitles.map(subtitleToText).reduce((acc, elem) => acc + elem + "\n\n", "").trim();
}
function subtitleToText({ startTime, endTime, text }, idx) {
    return `
${idx + 1}
${secondsToSubtitleTime(startTime)} --> ${secondsToSubtitleTime(endTime)}
${text}
  `.trim();
}
function secondsToSubtitleTime(time) {
    const hours = Math.floor(time / 3600);
    const minutes = Math.floor((time - hours * 3600) / 60);
    const seconds = Math.floor(time % 60);
    const millis = Math.floor((time % 1) * 1000);
    return `${hours.toString().padStart(2, "0")}:${minutes.toString().padStart(2, "0")}:${seconds.toString().padStart(2, "0")},${millis.toString().padStart(2, "0")}`;
}
